@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Poppins";
  font-display: swap;
  src: url("../fonts/poppinsregular.woff2") format("woff2"), url("../fonts/poppinsregular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Poppins";
  font-display: swap;
  src: url("../fonts/poppinsmedium.woff2") format("woff2"), url("../fonts/poppinsmedium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Poppins";
  font-display: swap;
  src: url("../fonts/poppinsbold.woff2") format("woff2"), url("../fonts/poppinsbold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.button {
  display: block;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  font-family: "Poppins", "Arial", sans-serif;
  color: #ffffff;
  text-transform: capitalize;
  text-decoration: none;
  background-color: #48b774;
  border: none;
  cursor: pointer;
}
.button:hover {
  background-color: #48b774;
  opacity: 0.9;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #48b774;
  opacity: 0.7;
}
.button:focus-visible {
  outline: 3px #48b774 solid;
  outline-offset: 2px;
}
.button:disabled {
  color: #ffffff;
  background-color: #e5e5e5;
  cursor: default;
}

.page {
  height: 100%;
}

.page-body {
  height: 100%;
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  font-family: "Poppins", "Arial", sans-serif;
  color: #000000;
  background-color: #ecf7f3;
}

.content-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  max-width: 1400px;
  min-height: 100%;
  margin: 0 auto;
  background-color: #ecf7f3;
}

.main-header {
  display: flex;
  padding: 10px;
}
.main-header.no-js {
  padding: 0;
}
@media (min-width: 768px) {
  .main-header {
    justify-content: space-between;
    padding: 30px 60px;
  }
}
@media (min-width: 1200px) {
  .main-header {
    justify-content: space-between;
    padding: 30px 146px;
  }
}

.main-header__navigation {
  display: flex;
}

.main-header__logo {
  display: flex;
  flex-shrink: 0;
  width: 131px;
  height: 40px;
  margin: auto auto auto 0;
}
@media (min-width: 768px) {
  .main-header__logo {
    margin-right: 0;
  }
}
.main-header__logo:hover {
  opacity: 0.8;
}
.main-header__logo:active {
  opacity: 0.5;
}
.main-header__logo:focus-visible {
  outline: none;
  opacity: 0.8;
}

.main-header__toggle {
  display: flex;
  flex-direction: column;
  align-self: center;
  margin-left: auto;
}
@media (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}
.main-header__toggle:hover {
  opacity: 0.6;
}
.main-header__toggle:active {
  opacity: 0.3;
}
.main-header__toggle:focus-visible {
  outline: 3px #766357 solid;
  outline-offset: 2px;
}
.main-header__toggle:disabled {
  opacity: 0.2;
}

.main-header__button {
  margin: auto 0;
  padding: 9px 23px;
  max-width: 150px;
  border-radius: 20px;
}

.main-container {
  flex-grow: 1;
}

.navigation__list {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  display: none;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #48b774;
  transform: translateY(-100%);
  transition: transform 0.3s ease;
  gap: 1px;
}
@media (min-width: 768px) {
  .navigation__list {
    position: relative;
    z-index: 0;
    display: flex;
    flex-wrap: wrap;
    align-self: center;
    background-color: #ecf7f3;
    transform: translateY(0);
    transition: none;
    gap: 40px;
  }
}
.is-open .navigation__list {
  display: grid;
  min-width: 320px;
  transform: translateY(0);
}
@media (min-width: 768px) {
  .is-open .navigation__list {
    display: flex;
  }
}
.no-js .navigation__list {
  position: relative;
  display: grid;
  transform: translateY(0);
  transition: none;
}

.navigation__item {
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
@media (min-width: 768px) {
  .navigation__item {
    display: flex;
    justify-content: center;
    box-shadow: none;
  }
}
@media (max-width: 767px) {
  .navigation__item:last-child {
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 90%, rgba(0, 0, 0, 0.2) 100%);
  }
}
@media (min-width: 768px) {
  .navigation__item:nth-child(4n-1) {
    grid-column-start: 4;
  }
}

.navigation__link {
  position: relative;
  display: block;
  padding: 17px 67px 12px 67px;
  font-weight: 400;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  color: #ffffff;
  text-transform: capitalize;
  text-decoration: none;
}
@media (min-width: 768px) {
  .navigation__link {
    padding: 0;
    max-width: 250px;
    font-size: 16px;
    line-height: 24px;
    color: #4a4a4a;
    background-color: #ecf7f3;
  }
}
@media (min-width: 768px) {
  .navigation__link--current::after {
    content: "";
    position: absolute;
    bottom: -4px;
    left: 50%;
    width: 21px;
    height: 1.5px;
    background-color: #fb9b69;
    transform: translate(-50%);
  }
}
.navigation__link:hover {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .navigation__link:hover {
    background-color: #ecf7f3;
  }
}
.navigation__link:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .navigation__link:active {
    color: rgba(0, 0, 0, 0.3);
    background-color: #ecf7f3;
  }
}
.navigation__link:focus-visible {
  outline: 3px #766357 solid;
}
@media (min-width: 768px) {
  .navigation__link:focus-visible {
    color: #81b3d2;
    background-color: #f2f2f2;
    outline: none;
  }
}

.hero {
  position: relative;
  display: grid;
  padding: 90px 19px 85px 19px;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto auto 578px auto auto;
}
@media (min-width: 768px) {
  .hero {
    padding: 50px 61px 0 40px;
  }
}
@media (min-width: 1200px) {
  .hero {
    padding: 50px 61px 0 183px;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: repeat(5, auto);
  }
}

.hero__text {
  max-width: 474px;
  margin: 0;
  margin-right: auto;
  margin-left: auto;
  padding-top: 31px;
  padding-bottom: 31px;
  font-weight: 700;
  font-size: 48px;
  line-height: 65px;
  color: #2d2d2d;
}
@media (min-width: 1200px) {
  .hero__text {
    margin-right: 0;
    margin-left: 0;
  }
}

.hero__text-color {
  color: #ff8345;
}

.hero_description {
  max-width: 444px;
  margin: 0;
  margin-right: auto;
  margin-bottom: 30px;
  margin-left: auto;
  font-weight: 500;
  font-size: 15px;
  line-height: 26px;
  color: #7c7c7c;
}
@media (min-width: 1200px) {
  .hero_description {
    margin-right: 0;
    margin-left: 0;
  }
}

.hero__button {
  margin-right: auto;
  margin-bottom: 50px;
  margin-left: auto;
  padding: 11px 21px;
  font-size: 15px;
  line-height: normal;
  border-radius: 30px;
}
@media (min-width: 1200px) {
  .hero__button {
    margin-left: 0;
  }
}

.hero__form {
  position: relative;
  width: 100%;
  max-width: 597px;
  margin-right: auto;
  margin-bottom: 40px;
  margin-left: auto;
}
@media (min-width: 1200px) {
  .hero__form {
    margin-right: 0;
    margin-left: 0;
  }
}

.hero__input {
  width: 100%;
  padding: 21px 50px 22px 40px;
  font-weight: 400;
  font-size: 12px;
  line-height: normal;
  font-family: "Poppins", "Arial", sans-serif;
  letter-spacing: 0.16px;
  background-color: #ffffff;
  border: none;
  border-radius: 100px;
  box-shadow: 0 20px 30px 0 rgba(219, 219, 219, 0.25);
}
@media (min-width: 560px) {
  .hero__input {
    font-size: 16px;
  }
}

.hero__input::placeholder {
  color: #a1a1a1;
}

.hero__form-button-wrapper {
  position: absolute;
  top: 50%;
  right: 6px;
  transform: translateY(-50%);
}

.hero__form-button {
  width: 55px;
  height: 55px;
  padding: 0;
  border-radius: 50px;
}
.hero__form-button::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 26px;
  height: 26px;
  background-image: url("../img/svg-image/search-icon.svg");
  transform: translate(-50%, -50%);
}

.hero__about {
  position: relative;
  max-width: 580px;
  margin-right: auto;
  margin-left: auto;
  padding: 10px 0 10px 113px;
  font-weight: 400;
  font-size: 16px;
  line-height: normal;
}
@media (min-width: 768px) {
  .hero__about {
    margin-bottom: 40px;
  }
}
@media (min-width: 1200px) {
  .hero__about {
    margin-right: 0;
    margin-left: 0;
  }
}

.hero__user-photo {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.hero__user-photo--one {
  left: 0;
  z-index: 4;
}
.hero__user-photo--two {
  left: 20px;
  z-index: 3;
}
.hero__user-photo--three {
  left: 43px;
  z-index: 2;
}
.hero__user-photo--four {
  left: 65px;
  z-index: 1;
}

.hero__about-description {
  margin: 0;
  padding: 0;
  color: #707070;
}

.hero__about-link {
  font-weight: 500;
  color: #fd905a;
  text-decoration: none;
}
.hero__about-link:hover {
  opacity: 0.6;
}
.hero__about-link:active {
  opacity: 0.3;
}

.hero__picture {
  display: flex;
  width: 100%;
  max-width: 580px;
  height: auto;
  margin-right: auto;
  margin-bottom: 30px;
  margin-left: auto;
  grid-row: 4/5;
}
@media (min-width: 1200px) {
  .hero__picture {
    width: auto;
    max-width: none;
    margin: 0;
    max-height: 590px;
    grid-column: 3/2;
    grid-row: 1/-1;
  }
}

.hero__image {
  display: block;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .hero__image--index {
    width: 461px;
    height: 350px;
  }
}
@media (min-width: 1200px) {
  .hero__image--index {
    width: 460px;
  }
}
@media (min-width: 768px) {
  .hero__image--inner {
    width: 457px;
    height: 78px;
  }
}
@media (min-width: 1200px) {
  .hero__image--inner {
    width: 460px;
    height: 80px;
  }
}

.burger-button {
  position: relative;
  z-index: 3;
  order: 3;
  width: 23px;
  height: 22px;
  padding: 0;
  background-color: inherit;
  border: none;
  cursor: pointer;
  transition: transform 0.3s ease;
}
.no-js .burger-button {
  display: none;
}

.burger-button__line {
  display: block;
  width: 23px;
  height: 4px;
  background-color: #48b774;
  transition: all 0.3s ease;
}
.burger-button__line:not(:last-child) {
  margin-bottom: 5px;
}

.is-open .burger-button__line {
  width: 27px;
  background-color: #ffffff;
}
.is-open .burger-button__line:nth-child(2) {
  position: relative;
  top: 48%;
  transform: rotate(45deg);
}
.is-open .burger-button__line:nth-child(3) {
  display: none;
}
.is-open .burger-button__line:nth-child(4) {
  position: relative;
  top: 10%;
  transform: rotate(-45deg);
}

.pictures {
  position: relative;
  max-width: 560px;
  padding-top: 40px;
  background-image: url("../img/background/ellipse.svg");
  background-repeat: no-repeat;
  background-position: top;
  background-size: 433px;
  scale: 70%;
}
@media (min-width: 560px) {
  .pictures {
    scale: 1;
  }
}

.pictures__woman {
  position: relative;
  z-index: 5;
  width: 100%;
  height: 100%;
  background-image: url("../img/background/woman.png");
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: cover;
}
@media (min-resolution: 2dppx) {
  .pictures__woman {
    background-image: url("../img/background/woman@2x.png");
  }
}

.pictures__flags {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin-right: auto;
  margin-left: auto;
}

.pictures__flag {
  position: absolute;
  top: 30%;
  left: 50%;
  z-index: 1;
  display: block;
}
.pictures__flag--one {
  top: 50%;
  left: 50%;
  animation: circleBounce-1 0.4s ease-in-out 3s forwards, rotated 7s linear 5s infinite;
}
.pictures__flag--two {
  top: 50%;
  left: 50%;
  animation: circleBounce-2 0.4s ease-in-out 3.4s forwards, rotated 7s linear 5s infinite;
}
.pictures__flag--three {
  top: 50%;
  left: 50%;
  animation: circleBounce-3 0.4s ease-in-out 3.8s forwards, rotated 7s linear 5s infinite;
}
.pictures__flag--four {
  top: 50%;
  left: 50%;
  animation: circleBounce-4 0.4s ease-in-out 4.2s forwards, rotated 7s linear 5s infinite;
}
.pictures__flag--five {
  top: 50%;
  left: 50%;
  animation: circleBounce-5 0.4s ease-in-out 4.6s forwards, rotated 7s linear 5s infinite;
}

@keyframes rotated {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes circleBounce-1 {
  0% {
    top: 50%;
    left: 50%;
  }
  100% {
    top: 37%;
    left: 3%;
    transform-origin: 270px 10px;
  }
}
@keyframes circleBounce-2 {
  0% {
    top: 50%;
    left: 50%;
  }
  100% {
    top: 10%;
    left: 5%;
    transform-origin: 250px 170px;
  }
}
@keyframes circleBounce-3 {
  0% {
    top: 50%;
    left: 50%;
  }
  100% {
    top: -8%;
    left: 42%;
    transform-origin: 50px 260px;
  }
}
@keyframes circleBounce-4 {
  0% {
    top: 50%;
    left: 50%;
  }
  100% {
    top: 11%;
    left: 71%;
    transform-origin: -130px 150px;
  }
}
@keyframes circleBounce-5 {
  0% {
    top: 50%;
    left: 50%;
  }
  100% {
    top: 37%;
    left: 76%;
    transform-origin: -160px 10px;
  }
}